import React, { useEffect, useState } from "react";
import { useNavigate, useLocation, Link } from "react-router-dom";
import { signOut } from "firebase/auth";
import { auth } from "../../../firebaseConfig";
import AOS from "aos";
import "aos/dist/aos.css";

export default function Header() {
  const navigate = useNavigate();
  const location = useLocation(); // ✅ to check current path
  const [userEmail, setUserEmail] = useState("");

  useEffect(() => {
    const storedEmail = sessionStorage.getItem("userEmail");
    if (!storedEmail) {
      navigate("/"); // redirect to login
    } else {
      setUserEmail(storedEmail);
    }

    AOS.init({ duration: 800, easing: "slide", once: true });
  }, [navigate]);

  const handleLogout = async () => {
    try {
      await signOut(auth); // sign out from Firebase
    } catch (err) {
      console.error("Error during sign out:", err);
    } finally {
      sessionStorage.removeItem("userEmail"); // clear session
      navigate("/"); // redirect to login
    }
  };

  return (
    <header className="site-navbar py-3" role="banner">
      <div className="container-fluid">
        <div className="row align-items-center">
          
          <div className="col-6 col-xl-2" data-aos="fade-down">
            <h1 className="mb-0">
              <Link to="/dashboard" className="text-white h2 mb-0">Tutorial</Link>
            </h1>
          </div>

          <div className="col-10 col-md-8 d-none d-xl-block" data-aos="fade-down">
            <nav className="site-navigation position-relative text-right text-lg-center" role="navigation">
              <ul className="site-menu js-clone-nav mx-auto d-none d-lg-block">
                <li className={location.pathname === "/dashboard" ? "active" : ""}>
                  <Link to="/dashboard">Home</Link>
                </li>
                <li className={location.pathname === "/about" ? "active" : ""}>
                  <Link to="/about">About</Link>
                </li>
                <li className={location.pathname === "/contact" ? "active" : ""}>
                  <Link to="/contact">Contact</Link>
                </li>
              </ul>
            </nav>
          </div>

          <div className="col-6 col-xl-2 text-right" data-aos="fade-down">
            <button onClick={handleLogout} className="btn btn-sm btn-black">
              {userEmail || "Logout"}
            </button>
          </div>

        </div>
      </div>
    </header>
  );
}
