import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { auth } from "../../firebaseConfig";
import {
  signInWithEmailAndPassword,
  createUserWithEmailAndPassword,
} from "firebase/auth";
import "./Login.css";

export default function Login() {
  const [panel, setPanel] = useState("login"); // toggle between login/register
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [cpassword, setCpassword] = useState("");
  const [name, setName] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate();

  // 🔐 Handle Login
  const handleLogin = async (e) => {
    e.preventDefault();
    setError("");
    try {
      await signInWithEmailAndPassword(auth, email, password);
      sessionStorage.setItem("userEmail", email);
      navigate("/dashboard"); // redirect on success
    } catch (err) {
      setError(err.message.replace("Firebase:", "").trim());
    }
  };

  // 🧾 Handle Register
  const handleRegister = async (e) => {
    e.preventDefault();
    setError("");
    if (password !== cpassword) {
      setError("Passwords do not match");
      return;
    }
    try {
      await createUserWithEmailAndPassword(auth, email, password);
      alert("Registration successful!");
      setPanel("login");
      setEmail("");
      setPassword("");
      setCpassword("");
      setName("");
    } catch (err) {
      setError(err.message.replace("Firebase:", "").trim());
    }
  };

  return (
    <div className="login-reg-panel">
      {/* LEFT: LOGIN info */}
      <div className="login-info-box">
        <h2>Have an account?</h2>
        <p>Login your account</p>
        <label id="label-register" htmlFor="log-reg-show">
          Login
        </label>
        <input
          type="radio"
          name="active-log-panel"
          id="log-reg-show"
          checked={panel === "login"}
          onChange={() => setPanel("login")}
        />
      </div>

      {/* RIGHT: REGISTER info */}
      <div className="register-info-box">
        <h2>Don't have an account?</h2>
        <p>Register your account for login</p>
        <label id="label-login" htmlFor="log-login-show">
          Register
        </label>
        <input
          type="radio"
          name="active-log-panel"
          id="log-login-show"
          checked={panel === "register"}
          onChange={() => setPanel("register")}
        />
      </div>

      {/* White Panel */}
      <div className={`white-panel ${panel === "register" ? "right-log" : ""}`}>
        {/* LOGIN form */}
        <div className={`login-show ${panel === "login" ? "show-log-panel" : ""}`}>
          <h2>LOGIN</h2>
          <br />
          {error && panel === "login" && (
            <div className="alert alert-danger py-1">{error}</div>
          )}
          <form onSubmit={handleLogin}>
            <div className="form-group mb-2">
              <input
                type="email"
                name="email"
                className="form-control"
                placeholder="Email"
                value={email}
                required
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className="form-group mb-2">
              <input
                type="password"
                name="password"
                className="form-control"
                placeholder="Password"
                value={password}
                required
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <div className="form-group">
              <button
                type="submit"
                className="btn btn-danger"
              >
                Login
              </button>
            </div>
          </form>
          <a className="forgot" href="fogot.php">Forgot password?</a>
        </div>

        {/* REGISTER form */}
        <div
          className={`register-show ${panel === "register" ? "show-log-panel" : ""}`}
        >
          <h2>REGISTER</h2>
          <br />
          {error && panel === "register" && (
            <div className="alert alert-danger py-1">{error}</div>
          )}
          <form onSubmit={handleRegister}>
            <div className="form-group mb-2">
              <input
                type="text"
                name="name"
                className="form-control name"
                placeholder="Username"
                value={name}
                required
                onChange={(e) => setName(e.target.value)}
              />
              <div className="invalid-feedback">Name is required</div>
            </div>
            <div className="form-group mb-2">
              <input
                type="email"
                name="email"
                className="form-control email"
                placeholder="Email"
                value={email}
                required
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className="form-group mb-2">
              <input
                type="password"
                name="password"
                className="form-control password"
                placeholder="Password"
                value={password}
                required
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <div className="form-group mb-2">
              <input
                type="password"
                name="cpassword"
                placeholder="Confirm Password"
                className="form-control cpassword"
                value={cpassword}
                required
                onChange={(e) => setCpassword(e.target.value)}
              />
            </div>
            <div className="form-group">
              <button
                type="submit"
                name="register"
                className="btn btn-danger"
              >
                Register
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
