import React, { useState, useEffect, useMemo } from "react";
import { Link, useLocation } from "react-router-dom";

export default function Aside() {
  const location = useLocation();
  const [openDropdown, setOpenDropdown] = useState(null);

  // ----- Define Navigation Items for Each Section -----
  const navData = useMemo(() => ({
    c: [
      { path: "/c/introduction", label: "Introduction" },
      { path: "/c/features", label: "Features" },
      { path: "/c/pstructure", label: "Program Structure" },
      { path: "/c/characterset", label: "Character Set" },
      { path: "/c/printfscanf", label: "Input Output (I/O)" },
      { path: "/c/datatype", label: "Data Type" },
      { path: "/c/comment", label: "Comments" },
      { path: "/c/token", label: "Tokens" },
      { path: "/c/operator", label: "Operator" },
      { path: "/c/typecasting", label: "Type Conversion" },
      { path: "/c/variable", label: "Variables" },
      { path: "/c/ifelse", label: "Decision Making" },
      { path: "/c/switch", label: "Switch Statement" },
      { path: "/c/loop", label: "Loops" },
      { path: "/c/breakcontinue", label: "Break & Continue" },
      { path: "/c/goto", label: "Goto Statement" },
      { path: "/c/array", label: "Array" },
      { path: "/c/functions", label: "Function" },
      { path: "/c/math", label: "Math" },
      { path: "/c/string", label: "String" },
      { path: "/c/pointer", label: "Pointer" },
      { path: "/c/structure", label: "Structure" },
      { path: "/c/union", label: "Union" },
      { path: "/c/enum", label: "Enumeration" },
      { path: "/c/recursion", label: "Recursion" },
      { path: "/c/storage", label: "Storage Classes" },
      { path: "/c/allocation", label: "Dynamic Memory Allocation" },
      { path: "/c/file", label: "File Handling" },
      {
        label: "Programs",
        children: [
          { path: "/c/ascii", label: "ASCII" },
          { path: "/c/star", label: "Star Patterns" },
          { path: "/c/evenodd", label: "Even or Odd" },
          { path: "/c/prime", label: "Prime or Not Prime" },
        ],
      },
    ],

    cpp: [
      { path: "/cpp/introduction", label: "Introduction" },
      { path: "/cpp/cvc", label: "C Vs C++" },
      { path: "/cpp/features", label: "Features" },
      { path: "/cpp/comments", label: "Comments" },
      { path: "/cpp/datatype", label: "Data Type" },
      { path: "/cpp/cincout", label: "Input Output (I/O)" },
      { path: "/cpp/variable", label: "Variables" },
      { path: "/cpp/access", label: "Access Modifier" },
      { path: "/cpp/oops", label: "Oops" },
      { path: "/cpp/oopspop", label: "Oops Vs Pop" },
      { path: "/cpp/inheritance", label: "Inheritance" },
      { path: "/cpp/constructor", label: "Constructor & Destructor" },
      { path: "/cpp/polymorphism", label: "Polymorphism" },
      { path: "/cpp/frnds", label: "Friend Class & Function" },
      { path: "/cpp/overriding", label: "Function Overriding" },
      { path: "/cpp/token", label: "Tokens" },
      { path: "/cpp/operator", label: "Operator" },
      { path: "/cpp/typecasting", label: "Type Conversion" },
      { path: "/cpp/expression", label: "Expression" },
      { path: "/cpp/ifelse", label: "Decision Making" },
      { path: "/cpp/switch", label: "Switch Statement" },
      { path: "/cpp/loop", label: "Loops" },
      { path: "/cpp/breakcontinue", label: "Break & Continue" },
      { path: "/cpp/goto", label: "Goto Statement" },
      { path: "/cpp/functions", label: "Function" },
      { path: "/cpp/inline", label: "Inline Function" },
      { path: "/cpp/array", label: "Array" },
      { path: "/cpp/string", label: "String" },
      { path: "/cpp/pointer", label: "Pointer" },
      { path: "/cpp/this", label: "This Pointer" },
      { path: "/cpp/structure", label: "Structure" },
      { path: "/cpp/enum", label: "Enumeration" },
      { path: "/cpp/recursion", label: "Recursion" },
      { path: "/cpp/storage", label: "Storage Classes" },
      { path: "/cpp/exceptions", label: "Exception Handling" },
      {
        label: "Program",
        children: [
          { path: "/cpp/fibonacci", label: "Fibonacci Series" },
          // { path: "/cpp/star", label: "Star Patterns" },
          { path: "/cpp/evenodd", label: "Even or Odd" },
          { path: "/cpp/prime", label: "Prime or Not Prime" },
          { path: "/cpp/palindrome", label: "Palindrome Number" },
          { path: "/cpp/factorial", label: "Factorial Number" },
          { path: "/cpp/armstrong", label: "Armstrong Number" },
          { path: "/cpp/reverse", label: "Reverse Number" },
          { path: "/cpp/swapping", label: "Swapping Number" },
        ],
      },
    ],

    php: [
      { path: "/php/introduction", label: "Introduction" },
      { path: "/php/features", label: "Features" },
      { path: "/php/comments", label: "Comments" },
      { path: "/php/echoprint", label: "Echo & Print" },
      { path: "/php/variable", label: "Variables" },
      { path: "/php/$variable", label: "$ & $$ Variables" },
      { path: "/php/datatype", label: "Data Type" },
      { path: "/php/constant", label: "Constant" },
      { path: "/php/magicconst", label: "Magic Constant" },
      { path: "/php/operator", label: "Operator" },
      { path: "/php/ifelse", label: "Decision Making" },
      { path: "/php/switch", label: "Switch" },
      { path: "/php/loop", label: "Loops" },
      { path: "/php/breakcontinue", label: "Break & Continue" },
      { path: "/php/function", label: "Function" },
      { path: "/php/recursive", label: "Recursive Function" },
      { path: "/php/math", label: "Math" },
      { path: "/php/array", label: "Array" },
      { path: "/php/arrayfunc", label: "Array Function" },
      { path: "/php/string", label: "String" },
      { path: "/php/getpost", label: "Get & Post" },
      { path: "/php/datetime", label: "Date & Time" },
      { path: "/php/include", label: "Include & Require" },
      { path: "/php/filesystem", label: "File System" },
      { path: "/php/fileupload", label: "File Upload" },
      { path: "/php/cookies", label: "Cookies" },
      { path: "/php/session", label: "Session" },
      { path: "/php/mail", label: "Mail() Function" },
    ],
  }), []);

  const section = location.pathname.split("/")[1] || "c";
  const navItems = useMemo(() => navData[section] || [], [navData, section]);

  // ✅ Run only on mount or route change
  useEffect(() => {
    const activeDropdown = navItems.find(
      (item) =>
        item.children &&
        item.children.some((child) => location.pathname === child.path)
    );
    if (activeDropdown) setOpenDropdown(activeDropdown.label);
  }, [location.pathname, navItems]);

  const handleDropdownToggle = (label) => {
    setOpenDropdown((prev) => (prev === label ? null : label));
  };

  return (
    <aside className="col-md-3 d-none d-md-block border-left">
      <div className="widget widget-support-forum p-md-3 p-sm-2">
        <span className="icon icon-forum"></span>
      </div>

      <ul className="aside-nav nav flex-column">
        {navItems.map((item) =>
          item.children ? (
            <li key={item.label} className="nav-item">
              <button
                className={`nav-link btn btn-link text-start pl-3 ${
                  openDropdown === item.label ? "text-dark fw-bold" : "text-muted"
                }`}
                onClick={() => handleDropdownToggle(item.label)}
              >
                {item.label} {openDropdown === item.label ? "▲" : "▼"}
              </button>

              {openDropdown === item.label && (
                <ul className="nav flex-column ms-3 animate__animated animate__fadeIn">
                  {item.children.map((child) => (
                    <li key={child.path} className="nav-item pl-3">
                      <Link
                        to={child.path}
                        className={`nav-link ${
                          location.pathname === child.path
                            ? "text-dark fw-bold"
                            : "text-muted"
                        }`}
                        style={{ fontSize: "0.85rem" }}
                      >
                        {child.label}
                      </Link>
                    </li>
                  ))}
                </ul>
              )}
            </li>
          ) : (
            <li key={item.path} className="nav-item">
              <Link
                to={item.path}
                className={`nav-link ${
                  location.pathname === item.path
                    ? "text-dark fw-bold"
                    : "text-muted"
                }`}
              >
                {item.label}
              </Link>
            </li>
          )
        )}
      </ul>
    </aside>
  );
}