import React, { useState, useEffect } from "react";
import './Main.css';
import './Swiper.css';
import Header from "./Include/Header";
import Footer from "./Include/Footer";
import AOS from "aos";
import "aos/dist/aos.css";

export default function Contact() {
  const [formData, setFormData] = useState({
    fname: '',
    lname: '',
    email: '',
    subject: '',
    message: ''
  });

  const [sending, setSending] = useState(false);
  const [statusMsg, setStatusMsg] = useState('');

  // handle input changes
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  // form submit handler
  const handleSubmit = async (e) => {
    e.preventDefault();

    // simple validation
    if (!formData.fname || !formData.email || !formData.message) {
      setStatusMsg('⚠️ Please fill all required fields.');
      return;
    }

    setSending(true);
    setStatusMsg('');

    try {
      const res = await fetch('https://apps.lnjbhilwara.com/api/main/notesContact.php', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData),
      });

      const text = await res.text();
      console.log("Raw response:", text);

      const result = JSON.parse(text);
      setStatusMsg(result.message || "Message sent!");

      if (result.status === 'success') {
        setFormData({ fname: '', lname: '', email: '', subject: '', message: '' });
      }
    } catch (err) {
      console.error("Error:", err);
      setStatusMsg('❌ Failed to send message. Please try again later.');
    } finally {
      setSending(false);
    }
  };

  // initialize AOS
  useEffect(() => {
    AOS.init({
      duration: 800,
      easing: 'ease-in-out',
      once: true,
      mirror: false,
    });
    AOS.refresh();
  }, []);

  return (
    <div className="Main">
      <div className="site-wrap">
        <Header />

        <div className="site-section" data-aos="fade">
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-md-7" data-aos="fade-up">
                <div className="row mb-5">
                  <div className="col-12">
                    <h2 className="site-section-heading text-center">Contact</h2>
                  </div>
                </div>
              </div>
            </div>

            <div className="row" data-aos="fade-up">
              {/* Contact Form */}
              <div className="col-lg-8">
                <form onSubmit={handleSubmit}>
                  <div className="row form-group mb-2">
                    <div className="col-md-6 mb-3 mb-md-0">
                      <label className="text-white" htmlFor="fname">First Name *</label>
                      <input
                        type="text"
                        id="fname"
                        name="fname"
                        className="form-control bg-black text-white"
                        value={formData.fname}
                        onChange={handleChange}
                      />
                    </div>
                    <div className="col-md-6">
                      <label className="text-white" htmlFor="lname">Last Name</label>
                      <input
                        type="text"
                        id="lname"
                        name="lname"
                        className="form-control bg-black text-white"
                        value={formData.lname}
                        onChange={handleChange}
                      />
                    </div>
                  </div>

                  <div className="row form-group mb-2">
                    <div className="col-md-12">
                      <label className="text-white" htmlFor="email">Email *</label>
                      <input
                        type="email"
                        id="email"
                        name="email"
                        className="form-control bg-black text-white"
                        value={formData.email}
                        onChange={handleChange}
                      />
                    </div>
                  </div>

                  <div className="row form-group mb-2">
                    <div className="col-md-12">
                      <label className="text-white" htmlFor="subject">Subject</label>
                      <input
                        type="text"
                        id="subject"
                        name="subject"
                        className="form-control bg-black text-white"
                        value={formData.subject}
                        onChange={handleChange}
                      />
                    </div>
                  </div>

                  <div className="row form-group mb-3">
                    <div className="col-md-12">
                      <label className="text-white" htmlFor="message">Message *</label>
                      <textarea
                        name="message"
                        id="message"
                        cols="30"
                        rows="7"
                        className="form-control bg-black text-white"
                        placeholder="Write your notes or questions here..."
                        value={formData.message}
                        onChange={handleChange}
                      ></textarea>
                    </div>
                  </div>

                  <div className="row form-group">
                    <div className="col-md-12">
                      <button
                        type="submit"
                        disabled={sending}
                        className="btn btn-success py-2 px-4"
                      >
                        {sending ? 'Sending...' : 'Send Message'}
                      </button>
                    </div>
                  </div>

                  {statusMsg && (
                    <p className="mt-3 text-white">{statusMsg}</p>
                  )}
                </form>
              </div>

              {/* Contact Info */}
              <div className="col-lg-3 ml-auto" data-aos="fade-left">
                <div className="mb-3">
                  <p className="mb-0 font-weight-bold text-white">Address</p>
                  <p className="mb-4">Parasnath Colony, Baral Road, Bijainagar [Ajmer]</p>

                  <p className="mb-0 font-weight-bold text-white">Phone</p>
                  <p className="mb-4">
                    <a href="tel:+918290367908" style={{ color: "#20c997" }}>+91 8290367908</a>
                  </p>

                  <p className="mb-0 font-weight-bold text-white">Email Address</p>
                  <p className="mb-0">
                    <a href="mailto:amanjain033@gmail.com" style={{ color: "#20c997" }}>
                      amanjain033@gmail.com
                    </a>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <Footer />
      </div>
    </div>
  );
}
