// src/App.js
import React from "react";
import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import ScrollToTop from "./Components/Main/Include/ScrollToTop";

// import only routes you need to test; keep names & paths exact
import Login from "./Components/Login/Login";
import Dashboard from "./Components/Main/Dashboard";
import About from "./Components/Main/About";
import Contact from "./Components/Main/Contact";

import Introduction from "./Components/Main/C/Introduction"; // must match folder case
// ...import other components the same way...

function App() {
  // detect localhost vs gh-pages
  const isLocalhost = window.location.hostname === "localhost" || window.location.hostname === "127.0.0.1";
  const basename = isLocalhost ? "/" : "/Notes";

  return (
    <Router basename={basename}>
      <ScrollToTop />
      <Routes>
        <Route path="/" element={<Login />} />
        <Route path="/dashboard" element={<Dashboard />} />
        <Route path="/about" element={<About />} />
        <Route path="/contact" element={<Contact />} />

        {/* C routes */}
        <Route path="/c/introduction" element={<Introduction />} />
        {/* keep other /c routes here if you want */}

        {/* C++ and PHP routes (example)
        <Route path="/cpp/introduction" element={<CCIntroduction />} />
        <Route path="/php/introduction" element={<PhpIntroduction />} />
        */}

        {/* fallback to home */}
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
    </Router>
  );
}

export default App;
