import React from "react";
import './Main.css';
import './Swiper.css';
import CLang from '../../Images/Language/c.png';
import CCLang from '../../Images/Language/c++.png';
import PhpLang from '../../Images/Language/php.png';
import JsLang from '../../Images/Language/js.jpg';
import Header from "./Include/Header";
import Footer from "./Include/Footer";

export default function Dashboard() {
  return (
    <div className="Main">
      <div className="site-wrap">
        <Header />
        <div className="container-fluid" data-aos="fade" data-aos-delay="500">
          <div className="row">
            {[
              { title: "C Language", img: CLang, link: "/c/introduction" },
              { title: "C++Language", img: CCLang, link: "/cpp/introduction" },
              { title: "PHP", img: PhpLang, link: "/php/introduction" },
              { title: "JavaScript", img: JsLang, link: "/javascript" },
            ].map((course, index) => (
              <div key={index} className="col-lg-4 mb-4" data-aos="fade-up" data-aos-delay={index * 100}>
                <div className="image-wrap-2 position-relative">
                  <div className="image-info">
                    <h2 className="mb-3">{course.title}</h2>
                    <a href={course.link} className="btn btn-outline-white py-2 px-4">More</a>
                  </div>
                  <img src={course.img} alt={course.title} className="img-fluid" style={{ height: 455 }} />
                </div>
              </div>
            ))}
          </div>
        </div>
        <Footer />
      </div>
    </div>
  );
}
